import React from "react";
import Banner from "./Banner";
import Footer from "./Footer";

import About from "../img/bherulalgameti_about.png";
import SocialMedia from "./SocialMedia";

function Main({ main }) {
  return (
    <main className="main-area">
      <Banner main={main} />
      <section className="section px-3" id="about" data-scroll-index="2">
        <div className="container">
          <div className="row">
            <div className="col-lg-6">
              <div
                className="about-img wow fadeInUp"
                data-wow-duration="0.5s"
                data-wow-delay="0.2s"
              >
                <img
                  src={About}
                  alt="Bheru Lal Gameti"
                  title="Bheru Lal Gameti"
                  className="img-fluid"
                />
              </div>
            </div>
            <div className="col-lg-6">
              <div
                className="about-inner wow fadeInUp"
                data-wow-duration="0.5s"
                data-wow-delay="0.1s"
              >
                <h6>About Me</h6>
                <p>
                  I'm Bheru Lal Gameti , a young 21 year old WordPress
                  Developer, Full Stack Developer, Blogger and Cyber Security
                  Expert.
                </p>
                <p>
                  I started my life as a perfectly imperfect child with many
                  flaws. I am the kind of person who loves learning and was
                  never concerned about the end result. In my free time, I love
                  to learn new things, play with my cat and i love to write
                  code. After learning HTML, CSS, JS and basic php in my high
                  school, I discovered my passion is "Creating solutions with
                  technology". After High School I decide start my carrier in IT
                  industry so i Join Computer Science Course Bachelors of
                  Computer Applications.
                </p>
                <p>
                  The main area of my expertise is back end development. PHP,
                  SQL, custom WordPress plugins Development, wordpress themes
                  Development from scratch and coding interactive layouts.
                </p>
                <a
                  href="https://www.linkedin.com/in/iambherulal"
                  target="_blank"
                  rel="noreferrer"
                  className="primary-btn"
                >
                  <span className="text">Contact Me</span>
                  <span className="icon">
                    <i className="fa fa-lightbulb"></i>
                  </span>
                </a>
              </div>
            </div>
          </div>
        </div>
      </section>
      <section
        className="section pb-minus-76 px-3 bg-primary-light"
        id="aboutsechool"
      >
        <div className="container">
          <div className="row justify-content-center">
            <div className="col-lg-10">
              <div className="section-heading">
                <span>About Me</span>
                <h2>THE WEB AFFAIR</h2>
              </div>
            </div>
          </div>
          <div className="row">
            <div
              className="col-lg-6 wow fadeInDown"
              data-wow-duration="0.5s"
              data-wow-delay="0.1s"
            >
              <div className="about-item">
                <div className="body">
                  <h3>ABOUT MY YEARS AS A STUDENT</h3>
                  <p>
                    I'm always been an average student. <b>Science</b> were my
                    favorite subjects. I was always keen to learn something new
                    and apply it. during high school and I had hard time
                    deciding what I want to do after school. In college, I
                    worked more on my interpersonal skills.{" "}
                  </p>
                  <p>
                    Curiosity has been my biggest teacher. I explore the
                    internet to learn techniques and best practices, and
                    according to me formal education just slowed this process of
                    self-learning. I spend 80% of my time learning things. New
                    tools, new languages, frameworks, vanilla code or design
                    practices. The remaining in implementation and taking
                    feedback’s from real users. Sleepless nights for pushing an
                    update, that’s what I live for.{" "}
                  </p>
                </div>
              </div>
            </div>
            <div
              className="col-lg-6 wow fadeInDown"
              data-wow-duration="0.5s"
              data-wow-delay="0.1s"
            >
              <div className="about-item">
                <div className="body">
                  <h3>ABOUT ME AND THE WEB</h3>
                  <p>
                    I did job for around 2-3 years at multiple companies. In
                    2021 I have started work with{" "}
                    <b>
                      <a
                        href="https://www.globally.digital/"
                        target="_blank"
                        rel="noreferrer"
                      >
                        globally.digital
                      </a>
                    </b>
                    , a WordPress agency focusing on high performance sites for
                    web publishers.
                  </p>
                  <p>
                    I believe that internet will change everything. I already
                    see that happening but in future it will be embedded even
                    deeper in our life. Internet is empowering everything we do,
                    therefore it’s becoming more complex. Users have more
                    options and less patience for the content. For me it is
                    fascinating how all this fits together. And I feel proud
                    every time when I open favorite my text editor (Microsoft
                    Visual Studio) and see "Code the Web".
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <section className="section pb-minus-70 px-3" data-scroll-index="3">
        <div className="container">
          <div className="row">
            <div className="col-lg-10">
              <div className="section-heading-left">
                <h2>How I develop WordPress websites</h2>
              </div>
            </div>
          </div>
          <div className="row">
            <div
              className="col-lg-4 col-md-6 wow fadeInLeft"
              data-wow-duration="0.5s"
              data-wow-delay="0.1s"
            >
              <div className="services-item">
                <div className="body">
                  <h4>01</h4>
                  <h5>Custom built to your specific needs</h5>
                  <p>
                    Your business needs more than a generic theme with minor
                    customization. Off the rack themes are bloated with features
                    you don’t need.Every time worked with a premade theme, it
                    works out to be more expensive than starting from scratch
                    and coding a custom WordPress theme
                  </p>
                </div>
              </div>
            </div>
            <div
              className="col-lg-4 col-md-6 wow fadeInUp"
              data-wow-duration="0.5s"
              data-wow-delay="0.2s"
            >
              <div className="services-item">
                <div className="body">
                  <h4>02</h4>
                  <h5>Minimal plugins</h5>
                  <p>
                    Plugins are easy to install but hard to remove. Websites
                    with dozens of plugins can become a house of cards – you
                    don’t know what will happen if you deactivate one. I use
                    industry standard plugins when they make sense (like a
                    contact form), and build your unique functionality from
                    scratch.
                  </p>
                </div>
              </div>
            </div>
            <div
              className="col-lg-4 col-md-6 wow fadeInRight"
              data-wow-duration="0.5s"
              data-wow-delay="0.1s"
            >
              <div className="services-item">
                <div className="body">
                  <h4>03</h4>
                  <h5>As easy to edit as a Word document</h5>
                  <p>
                    The purpose of a CMS is to let you manage content yourself,
                    without the need of a developer. I focus on crafting the
                    backend editing experience to be as easy to use as the
                    frontend.
                  </p>
                </div>
              </div>
            </div>
            <div
              className="col-lg-4 col-md-6 wow fadeInLeft"
              data-wow-duration="0.5s"
              data-wow-delay="0.1s"
            >
              <div className="services-item">
                <div className="body">
                  <h4>04</h4>
                  <h5>Faster than your competitors</h5>
                  <p>
                    A 0.1 second improvement of mobile site speed can increase
                    conversion rates by 8-10%. I can deliver speed and
                    efficiency no off-the-shelf theme can match.
                  </p>
                </div>
              </div>
            </div>
            <div
              className="col-lg-4 col-md-6 wow fadeInUp"
              data-wow-duration="0.5s"
              data-wow-delay="0.2s"
            >
              <div className="services-item">
                <div className="body">
                  <h4>05</h4>
                  <h5>Accessible to all users</h5>
                  <p>
                    15% of visitors experience some level of disability, and
                    accessibility lawsuits are increasing. I build websites that
                    meet the Web Content Accessibility Guidelines (WCAG) 2.1
                    standards.
                  </p>
                </div>
              </div>
            </div>
            <div
              className="col-lg-4 col-md-6 wow fadeInRight"
              data-wow-duration="0.5s"
              data-wow-delay="0.1s"
            >
              <div className="services-item">
                <div className="body">
                  <h4>06</h4>
                  <h5>Built to last</h5>
                  <p>
                    The web changes fast, but you don’t need a new website every
                    few years. I can build you a solid foundation that will
                    adapt to your future needs.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <section
        className="section bg-dark-blue pb-30 px-3"
        data-scroll-index="4"
      >
        <div className="container">
          <div className="row justify-content-center">
            <div className="col-lg-10">
              <div className="section-heading" style={{ marginBottom: "50px" }}>
                <span>How I Work</span>
                <h2>How we solve your problems</h2>
              </div>
            </div>
          </div>
          <div className="row justify-content-center text-center">
            <div className="col-md-10">
              <div className="how-it-work-body text-white">
                <p>
                  A successful web project requires deep collaboration between
                  design and development. My two design partners and I have
                  worked together on over 100 projects over the past five years.
                </p>
              </div>
            </div>
          </div>
          <div className="row">
            <div
              className="col-md-4 wow fadeInUp"
              data-wow-duration="0.7s"
              data-wow-delay="0.2s"
            >
              <div className="how-i-work-item">
                <div className="number">
                  <span>01</span>
                </div>
                <div className="text">
                  <h5>Discovery</h5>
                  <p>
                    We sit down and figure out exactly what you need for your
                    site. This involves a questionnaire, design audit, and
                    technical review of your current site. At the end of this
                    phase, we create a plan for your new website based on your
                    specific needs.
                  </p>
                </div>
              </div>
            </div>
            <div
              className="col-md-4 wow fadeInUp"
              data-wow-duration="0.7s"
              data-wow-delay="0.4s"
            >
              <div className="how-i-work-item">
                <div className="number">
                  <span>02</span>
                </div>
                <div className="text">
                  <h5>Design</h5>
                  <p>
                    We design the key pages of your site based on our plan from
                    discovery. We create a style guide and begin design
                    exploration of key pages. We’ll continue iterating until we
                    have mockups that meet the plan requirements.
                  </p>
                </div>
              </div>
            </div>
            <div
              className="col-md-4 wow fadeInUp"
              data-wow-duration="0.7s"
              data-wow-delay="0.6s"
            >
              <div className="how-i-work-item">
                <div className="number">
                  <span>03</span>
                </div>
                <div className="text">
                  <h5>Development</h5>
                  <p>
                    We turn the mockups into reality. We copy your existing
                    site, remove all unnecessary plugins, and build a fully
                    custom theme. We integrate performance and accessibility
                    audits throughout the development process.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <section className="section px-3" id="contact" data-scroll-index="5">
        <div className="container">
          <div className="row justify-content-center">
            <div className="col-lg-6">
              <div className="section-heading">
                <span>Contact</span>
                <h2>Get In Touch</h2>
              </div>
            </div>
          </div>
          <div className="row">
            <div className="col-lg-6">
              <div className="contact-info-item">
                <div className="icon">
                  <span className="fa fa-envelope-open-text"></span>
                </div>
                <div className="body">
                  <h5>Email</h5>
                  <p>
                    <a href="mailto:iambherulal@gmail.com">
                      iambherulal@gmail.com
                    </a>
                  </p>
                </div>
              </div>
            </div>
            <div className="col-lg-6">
              <div className="contact-info-item text-center">
                <div className="body flex-grow-1">
                  <h5>You can find me everywhere</h5>
                  <ul className="hero-social-list social-list mb-3">
                    <SocialMedia
                      title="Bheru lal Gameti Github"
                      url="https://www.github.com/iambherulal"
                      icon="fab fa-github"
                    />
                    <SocialMedia
                      title="Bheru lal Gameti Linkedin"
                      url="https://www.linkedin.com/in/iambherulal"
                      icon="fab fa-linkedin"
                    />
                    <SocialMedia
                      title="Bheru lal Gameti Instagram"
                      url="https://www.instagram.com/iambherulal"
                      icon="fab fa-instagram"
                    />
                    <SocialMedia
                      title="Bheru lal Gameti WordPress"
                      url="https://profiles.wordpress.org/iambherulal"
                      icon="fab fa-wordpress"
                    />
                    <SocialMedia
                      title="globally digital"
                      url="https://www.globally.digital"
                      icon="fas fa-globe-asia"
                    />
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <Footer />
    </main>
  );
}

export default Main;
